/*매장 찾기*/
#map {
    height: 100%;
}





* 
    .header,
    .footer,
    .content,
    body,
    div,
    nav,
    ul,
    li,
    p {
        margin: 0;
        padding: 0;
        font-family: 'Noto Sans KR Black', "Noto Sans KR",Helvetica, Arial, 'Apple SD Gothic Neo', 'Apple SD 산돌고딕 Neo', 'Malgun Gothic', '맑은 고딕', sans-serif;
        font-optical-sizing: auto;
        font-style: normal;
        font-variation-settings:"wdth" 100;
    }

    button {
        cursor: pointer;
    }


/* index-total */
.total-view-button{
    padding: 4px 10px;
    font-size: 13px;
    color: #222;
    line-height: 1.429em;
    border: 1px solid #e2e2e2;
    border-radius: 15px;
}


/* 컨텐츠 가운데 정렬  */
.center-layout{
    width: 1440px;
    margin: 0 auto;
}

.center-title{
    padding: 40px 15px 10px;
}



/* Moviechart, event,special => title style*/
.content-title-style{
    display: flex;
    justify-content: space-between;
}


/* 무비차트/상영예정작 타이틀 */
.content-title-style-font{
    font-size: 1.5em;
    font-weight: 600;
    margin: 20px 0;
}

.font-color-main{color:var(--podium-cds-color-black);}
.font-color-gray{color:var(--color-gray-222)}
/* .special-event-list {padding: 0 20px;} */
.btn-main-color{
    border: 1px double var(--color-white);
    background-color:var(--color-button);
    color:var(--color-white);
}

.login-form p{
    font-size: 13px;
}

