/*******************************************

*  title : login form style

********************************************/



.center-title {
    opacity: .8;
    color: var(--color-login-font);
    text-align: center;
}
.login-form {
    /* border: 1px solid green; */
    width: 100%;
    margin: 0 auto;
    margin-bottom: 50px;
    color: var(--color-login-font);
}
.login-form ul {
    list-style-type: none;
    width: 500px;
    display: flex;
    flex-direction:column;
    justify-content: flex-start;
}
.login-form ul li {
    padding: 10px;
    padding-left: 70px;
}
.login-form ul li:last-child div{
    display: flex;
    justify-content: space-between;
}

.login-form ul li:last-child, label{
    font-size: 13px;
    display: flex;
    justify-content: space-between;
    margin-left: 5px;
}
.login-form-message {
    font-size: 11pt; 
    color: var(--color-font-primary);
    margin-bottom: 10px;
}
.login-form-input {
    border: 1px solid var(--podium-cds-typography-body2-strong);
    border-radius: 8px;
    padding: 5px 10px;
}
.login-form-input-icons {    
    padding: 0 15px 0 10px;
    border-right: 1px solid silver;
}
.login-form-input input { 
    width: 80%; 
    border: none;
    padding: 7px 10px;
    margin-left: 10px;
    outline: none;
}
.login-button {
    width: 350px;
    padding: 15px 10px;
    outline-color: var(--color-buttonbg);
    outline-style: solid;
    outline-width: 1px;
    background-color: salmon;
    color: white;
}
.login-button-naver {
    width: 350px;
    padding: 15px 10px;
    outline-color: var(--color-buttonbg); 
    outline-style: solid;
    outline-width: 1px;
    background-color: green;
    color: white;
}
.login-form-checkbox {
    width: 120px;
    padding: 7px 0;
    border: none;
    outline: none;
    font-size: 11pt;
}
.login-form ul li:nth-child(5) {
    display: flex;
    gap: 5px;
    align-items: center;
    width: 350px;
    margin: 0 auto;
    padding: 8px 0;
    font-size: 10pt;
}
.loginplus-image {
    /* border: 1px solid red; */
    padding-top: 10px;
}
.loginplus-image img {
    width: 360px;
    height: 370px;
}




.login-form-input{
    width: 300px;
    padding: 5px 10px;
    display: flex;
    align-items: center;
}


.login-form input:not(input[type="checkbox"]){
    width: 230px;
    padding: 5px;
    margin-left: 10px;
    border: none;
    outline: none;
}

.login-form ul li:nth-child(5){
    font-size: 9px;
}
.login-form ul li:nth-child(5),
.login-form button{
    width: 300px;
    padding: 10px;
}
.login-form form{
    width: 300px;
    color: rgb(137, 137, 137);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.login-form ul{
    list-style-type: none;
    width: 420px;
}
.login-form ul li{
    padding: 10px;
}

.login-form input:not(input[type="checkbox"]),/*input에서 체크박스 제외하고 적용*/
.login-form button{
    font-size: 15px;
    padding: 10px;
}


.login-form input[type="text"],
.login-form input[type="password"]{
    padding-left: 20px;
}
.login-form button{
    width: 300px;
    background-color: var(--podium-cds-color-black);
    border-radius: 50px;
    color: #fff;
    display: block;
    font-size: 15px;
    font-weight: 500;
}

.login-form ul li:nth-child(5){
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 9pt;
}



.login-form ul > li:nth-child(5) div:last-child > :nth-child(2){
    padding-right:10px ;
}
.login-form ul > li:nth-child(5) div:last-child > a{
    color: var(--color-login-font);
    font-weight: 700;
}


.login-form i{
    border-right: 1px solid rgb(235, 235, 235);
    padding: 15px;
}

.selected-country-label{
    position: relative;
    padding: 15px 10px 0;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: flex-start;
    overflow: clip;
    color: rgb(17, 17, 17);
}
.selected-country-label select{
    font-size: 15px;
    width: max-content;
    display: inline-block;
    color: rgb(117, 117, 117);
    margin-left: 10px;
    background: transparent;   /* 배경을 투명하게 설정 */
    border: none    /* 적당한 테두리 스타일 */
}
.selected-country-label label{
    font-size: 15px;
    color: rgb(17, 17, 17);

}



