
/**********************************************************

*  title :  header style

**********************************************************/

header{
    width: 100%;
    height: 160px;
    margin: 0 auto; /*가운데 정렬*/
    color: var(--podium-cds-color-black);
}
.header-top, .header-bottom-content{ /* ',' 동일하게 적용*/
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1440px;
    margin: 0 auto;
    padding: 13px 0;

}













.header-top-logo{
    position: relative;
    width: 320px;
}

.header-top-logo > a > img{
    width: 120px;
}
.header-top-logo > span{
    position: absolute;
    top:15px;
    letter-spacing: 3px; /*글자간 간격 조정*/
    color: var(--podium-cds-color-black);
    font-weight: 500;
}

/* header-bottom 상단 대메뉴정렬 */
.header-top ul, .header-bottom ul{  /*메뉴리스트 ul에 flex적용*/
    display: flex;
    list-style-type: none;

}

.header-top ul li, button{
    font-size: 13px;
    font-weight: 600;
    margin: auto;
    background: none;
}
.header-top ul li a{
    text-decoration: none;
    color: var(--podium-cds-color-black);
    text-align: center;
}


.header-bottom li a{
    font-size: 16px;
    font-weight: 700;
    color: #222;
    text-decoration: none;
}

.vertical-line{
    border-right: 1px solid var(--podium-cds-color-black);
    height: 12px;
    margin: 12px;
}



/****상단 메뉴 라인***/


.header-bottom ul {
    color: var(--podium-cds-color-black);
    font-weight: 600;
    list-style: none;
    padding: 0;
    margin: 0;
}

.header-bottom li {
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
    position: relative;
}

/* .header-bottom li::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px; 
    background-color:var(--podium-cds-color-black); 
    transition: width 0.3s ease;
}

.header-bottom li:hover::after {
    width: 100%; 
} */


.header-bottom-content li a {
    position: relative; 
    text-decoration: none; 
    color: var(--podium-cds-color-black); 
}

.header-bottom-content li a::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    margin-bottom: -8px;
    background-color: var(--podium-cds-color-black);
    transition: width 0.3s ease;
}

.header-bottom-content li a:hover::after {
    width: 100%;
}


/* right-검색, 즐겨찾기, 장바구니*/

.user-tools-container{
    display: flex;
    justify-content: right;
    width: 940px;
}

.user-tools-container div i{
    color: #111111;
}

.favorite-button, .cart{
    margin-left: 25px;
}


.header-bottom-search{
    width: 168px;
    display: flex;
    justify-content: space-between;
    padding: 0 5px;
}



.header-bottom-search input {
    width: 127px;
    border: none;           
    background-color:#fff; 
    color: #111;           
    padding: 8px 12px;      
    font-size: 16px;       
    border-radius: 24px;     
    transition: color 0.3s ease, background-color 0.3s ease; 
}

.header-bottom-search input:hover {
    color: #111;           
    background-color:#dfdfdf;
}




.header-bottom-search i{
    width: 30px;
    height: 24px;
    margin: 5px;
    font-size: 1.3em;
    color: var(--podium-cds-color-black);
    cursor: pointer;
}

.main-title{
    font-size: 16px;
    font-weight: 400;
}

.main-title p:first-child{
    font-size: 16px;
    font-weight: 600;
    margin-top: 60px;
}


.main-title p:nth-child(2){
    font-size: 3rem;
    font-weight: 900;
    margin: 10px 0;
}


.user-tools-container svg{
    font-size: 30px;
    color: var( --podium-cds-color-black);
    font-weight: 400;
}

/* header end */





/**video main**/

#app {
    height: 100%;
}
html,
body {
    position: relative;
    height: 100%;
}

body {
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
