/**swiper**/

#app {
    height: 100%;
}
html,
body {
    position: relative;
    height: 100%;
}

body {
    font-size: 14px;
    color: #000;
    margin: 0;
    padding: 0;
}

.swiper {
    width: 100%;
    height: 800px;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.swiper-button-next, .swiper-rtl .swiper-button-prev{
display: none;
}


.swiper-button-prev:after, 
.swiper-button-next:after {
    color:var(--podium-cds-color-white)
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    background-color: var( --podium-cds-color-white);
}
.swiper-pagination-bullet span {
    color: var( --podium-cds-color-white);
}





/**********goto top***********/
.goto-top{
    border: 1px solid silver;
    width: 100px;
    height:100px;
    text-align: center;
    line-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 40px;
    bottom:40px;
    background-color: #ec240a;
    color: var(--color-white);
    font-size: 20px;
    font-weight: 600;
    border-radius: 100%;
    
}
.goto-top a{
    cursor: pointer;
    text-decoration: underline;
}






