/**********************************************************

*  title :  login form style

**********************************************************/

.login-form-input{
    border: 2px solid silver;
    width: 270px;
    padding: 5px 10px;
    display: flex;
    align-items: center;
}


.login-form input:not(input[type="checkbox"]){
    width: 230px;
    padding: 5px;
    margin-left: 10px;
    border: none;
    outline: none;
}

.login-form ul li:nth-child(5){
    font-size: 9px;
}
.login-form ul li:nth-child(5),
.login-form button{
    width: 270px;
    padding: 10px;
}
.login-form form{
    width: 800px;
    color: rgb(137, 137, 137);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.login-form ul{
    list-style-type: none;
    width: 420px;
}
.login-form ul li{
    padding: 10px;
}

.login-form input:not(input[type="checkbox"]),/*input에서 체크박스 제외하고 적용*/
.login-form button{
    width: 263px;
    padding: 10px;
}


.login-form input[type="text"],
.login-form input[type="password"]{
    padding-left: 20px;
}
.login-form button{
    outline-color:var(--color-button);
    outline-style: solid;
    outline-width:1px;
}

.login-form ul li:nth-child(5){
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 9pt;
}



.login-form ul > li:nth-child(5) div:last-child > :nth-child(2){
    padding-right:10px ;
}
.login-form ul > li:nth-child(5) div:last-child > a{
    color: var(--color-login-font);
    font-weight: 700;
}




/**********************************************************

*  title :  Join Form style

**********************************************************/
.login-form i{
    border-right: 1px solid rgb(235, 235, 235);
    padding: 15px;
}
.join-form form{
    width: 500px;
    margin: 0 auto;
    /* display: flex;
    justify-content: space-between;
    color: rgb(137, 137, 137); */
}
.join-form ul{
    list-style-type: none;
}
.join-form ul li{
    list-style-type: none;
    padding-top: 10px;
}

.join-form ul li div{
    border-radius: 8px;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
}



.join-form ul li div:not(.join-form ul li:nth-child(6) div),
.join-form ul li:nth-child(6) input,
.join-form ul li:nth-child(6) select{
    border:1px solid var(--color-gray-222);
    border-radius:8px;
    padding:10px;
}
.join-form ul li:nth-child(6) span{
    margin: 0 20px;
}


.join-form ul li:nth-child(6) span{
    margin: 0 20px;
}
.join-form ul li div:first-child > div,
.join-form ul li div:nth-child(6) > div{
    display: flex;
    justify-content:space-between;
    align-items: center;
}

.join-form ul li label{
    display: inline-block;
    font-size: 0.9em;
    font-weight: 700;
    text-indent: 7px;
    margin-right: 10px;
}

.join-form ul li label + span{
    font-size: 0.7em;
    font-weight: 300;
    color: gray;
}

.join-form ul li:first-child input:focus{
    border: none;
    outline: none;
}

.join-form ul li:first-child input:focus{
    width: 80%;
    padding-left: 10px;
}

.join-form ul li:first-child input{
    width: 80%;
    padding-left: 10px;
}
.join-form ul li input{
    border: 1px solid white;
    width: 100%;
    padding: 7px 10px;
}
.join-form ul li:first-child button,
.join-form ul li:last-child button{
    /* border: 1px solid blue; */
    width: 100px;
    padding: 7px 12px;
    background-color: var(--color-button);
    color: var(--color-white);
    border-radius: 7px;
    margin-left: 10px;
    border: none;
}
.join-form ul li:lst-child button{
    border-radius: 0;
    width: 150px;
}
.join-form ul li:last-child button:last-child{
    background-color: var(--color-gray-222);
}

.join-form ul li:last-child{
    padding: 30px 0 10px;
    text-align: center;
}






/**********************************************************

*  title :  support style

**********************************************************/

.support-content{
    width: 700px;
    margin:0 auto;
    color: var(--color-primary);
    font-size: 14px;
}

/* 검색 카테고리 */
.support-content > div  {
    margin: 30px 0 20px;
}
.support-content > div select,
.support-content > div button,
.support-content > div input
{
    padding: 5px 15px;
}
.support-content > div select{
    padding: 5px 25px;
}
.support-content > div > button{
    background-color: #000;
    color:var(--color-white);
    outline:  1px solid #000;
}
.support-content > nav ul{
    border-bottom: 3px solid var(--color-primary);
    display: flex;
    list-style-type: none;
}
.support-content > nav ul a{
    width:90px;
    border-bottom: 3px solid var(--color-primary);
    border: 1px solid var(--color-white);
    display: inline-block;
    font-size: 13px;
    font-weight: 600;
    text-align: center;
    padding: 7px 0;
    text-decoration: none;
    color: var(--color-white);
    background-color: var(--color-gray-222);
}
.support-content > nav ul li:first-child a{
    background-color: var(--color-button);
    border-left: 1px solid var(--color-button);
    ;
}
.support-content > nav + p{
    margin: 40px 0 20px;
}
.support-content > table{
    width: 700px;
    text-align: center;
}
.support-content > table th
{
    padding: 10px 0;
}
.support-content >table td
{
    padding: 14px 0;
}
.support-content > table thead th:first-child
{
    width: 12%;
}
.support-content > table thead th:nth-child(2),
.support-content > table thead th:nth-child(4),
.support-content > table thead th:nth-child(5)
{
    width: 16%;
}
.support-content > table thead th:nth-child(3)
{
    text-align: left;
    text-indent: 8px;
}




/* 테이블 제목 세로 경계라인 */
.support-content >table,
.support-content >table th,
.support-content >table td
{
    border-collapse: collapse;
}

.support-content > table thead th,
.support-content > table thead th
{
    background-color: rgb(234,234,234);
    border-top: 1px solid silver;
    border-bottom: 1px solid silver;
    border-left: 1px solid rgb(234,234,234);
    border-right: 1px solid rgb(234,234,234);
}

.support-content > table thead th
{
    background-color: rgb(234,234,234);
    border-top: 1px solid silver;
    border-bottom: 1px solid silver;
}
.support-content > table tbody td:nth-child(3) a{
    text-decoration: none;
    color: var(--color-primary)
}
.support-content > table tbody td:nth-child(3) a:hover{
    text-decoration:underline;
    font-weight: 700;
}
.support-content > table tbody tr:last-child td{
    border-bottom: 1px solid silver ;
}
.support-content > table tfoot td{
    padding: 20px 0;
    font-size: 1.2em;
    font-weight: 500;
}







