/**********************************************************

*  title :  footer style

**********************************************************/
footer {
    background-color: var(--color-footerbg);
    color:var(--podium-cds-typography-body2-strong);  
}

.footer-content { 
    display: block;
    text-align: center;
}

.footer-content-top img {
    margin-top: -60px;
}

.footer-intro{
    width: 1440px;
    margin: 0 auto;
    margin-top: 100px;
}

.footer-intro > ul {
    height: 60px;
    display: flex;
    justify-content: left;
    align-items: center;
    list-style-type: none;
}
.footer-intro > ul a {
    font-size: 12px;
    font-weight: bold;
    text-decoration: none;
    font-size: 14px;
    color: var(--podium-cds-typography-body2-strong);

}
.footer-intro > ul a:hover {
    text-decoration: underline;
}


.footer-intro > ul li:nth-child(8) a {
    font-weight: 600;
    text-decoration: underline;
}
.footer-intro ~ p {
    font-size: 12px;
    font-weight: 600;
    padding: 3px 0;
}
.p2 { margin-bottom: 20px;}
.p2 > a {
    text-decoration: none;
    cursor: default;
}

.footer-info {
    display: flex;
    justify-content: center;
    gap: 12px;
    font-size: 13px;
    line-height: 25px;
    height: 1px;
    background: var(--podium-cds-color-border-tertiary);
}


.f-con, .korea-legal-footer-section{
    width: 50%;
    height: 200px;
    text-align: left;
    padding: 50px;
}

.korea-legal-footer-section a{
    font-weight: var(--podium-cds-font-weight-regular);
    -webkit-text-decoration: underline;
    text-decoration: underline;
    -webkit-text-decoration-color: var(--podium-cds-color-text-secondary);
    text-decoration-color: var(--podium-cds-color-text-secondary);
    color: var(--podium-cds-color-black);
}


.footer-list ul{
    display: flex;
    justify-content: left;
    padding: 50px 0;
}

.footer-list li{
    margin-left: 20px;
}

button {
    border:none;
    
}





