/*******************************************

*  signup form style

********************************************/

.signup-con{
    display: flex;
    justify-content: center;
    width: 100%;
}

.signup-form h1{
    color: #000000;
    font-size: 30px;
    font-weight: normal;
    line-height: 32px;
    text-align: left;
    margin: 30px 0;
}


.signup-form {
    width: 500px;
    margin: 0 auto;
}

.signup-form ul {
    list-style-type: none;
}
.signup-form ul li {
    padding-top: 15px 0 10px;
}

.signup-form ul li:last-child {
    text-align: right;
    margin:20px 0  50px ;
}

.signup-form ul li div:not(.signup-form ul li:nth-child(6) div)
{
    border: 1px solid var(--podium-cds-typography-body2-strong);
    border-radius: 8px;
    padding: 5px 10px;
}
.signup-form ul li:nth-child(6) select {
    width: 40%;
}
.signup-form ul li:nth-child(6) input,
.signup-form ul li:nth-child(6) select {
    border: 1px solid var(--podium-cds-typography-body2-strong);
    border-radius: 4px;
    padding: 12px 10px;
}
.signup-form ul li:nth-child(6) span {
    margin: 0 20px;
}
.signup-form ul li:first-child > div,
.signup-form ul li:nth-child(6) > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.signup-form ul li label b{
    display: inline-block;
    margin:0 10px 3px;
    font-size: 14px;
    padding: 15px 10px 0 0;
    line-height: 20px;
    font-weight: 400;
    font-style: normal;
    text-transform: none;
}


.signup-form ul li label + span {
    /* border: 1px solid red; */
    font-size: 0.7em;
    font-weight: 300;
    color: #797878;
    margin-left: 10px;
}

.signup-form ul li:first-child input{
    border: 1px solid white;
    width: 80%;
    padding-left: 10px;
    font-size: 15px
}

.signup-form ul li input {
    border: 1px solid white;
    width: 100%;
    padding: 7px 10px;
    font-size: 15px;
}

.signup-form ul li:first-child input:focus,
.signup-form ul li input:focus, .store-search input,.nike-shop-root .center-search input {
    border: none;
    outline: none;
}


.signup-con button{
    background-color: var(--podium-cds-color-black);
    border-radius: 50px;
    color: #fff;
    display: block;
    max-width: 130px;
    margin: 0 auto;
    padding: 10px 12px;
    font-size: 15px;
    font-weight: 500;
}

form li:last-child button{
    display: inline-block;
    margin-left: 10px;
}




