
/**********************************************************

*  title :  products style

**********************************************************/
.wall-header-content{
    display: block;   
    margin: 0 auto;
    margin-top: 50px;
    color: var(--podium-cds-color-black); 

}
.nike-shop-root{
    max-width: 1440px;
    margin: 0 auto;
}

.wall-header-title{
    display: flex;
    justify-content: space-between;
}

.wall-title, .wall-header-item-count, .sort{
    font-size: 28px;
    font-weight: 600;
}


.customer-header-title{
    color: var(--podium-cds-color-black);
    font-weight: 600;
    font-size: 16px;
    text-align: center;
    line-height: 1.3;
}

.customer-header-title h1{
    margin-bottom: 20px;
}


.center-search{
    display: flex;
    justify-content: space-between;
    border: 1px solid var(--podium-cds-typography-body2-strong);
    border-radius: 8px;
    padding: 5px 10px;
    width: 480px;
    height: 55px;
    margin: 0 auto;
}


.center-search input{
    font-size: 16px;
    border: none;
}

.center-search svg {
    width: 30px;
    height: 30px;
    margin: auto 0
}








/*아이템 리스트*/
.results-body{
    display: flex;
    width: 1440px;
    margin: 0 auto;
    margin-top: 15px;
}




/************ left nav **********/

.left-nav-wrapper{
    width: 240px;
    padding: 0 0 1em 48px;
    color: var(--podium-cds-color-black);
}

.left-nav-wrapper ul li{
    color: var(--podium-cds-color-black);
    font-size: 16px;
    font-weight: 600;
    white-space: normal;
    word-wrap: break-word;
    line-height: 1.35em;
    padding-bottom: 12px;
    padding-right: 1.1em;
    list-style-type: none;
    color: inherit; 
}

.left-nav-wrapper ul li a{
    text-decoration: none; 
    color: inherit; 
}


/* 제품 리스트*/
.products-grid{
    width: 1440px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
	/* grid-template-columns: 1fr 1fr 1fr 1fr 1fr */
    gap: 30px; 
    padding-left: 40px;
    padding-right: 48px;
    margin-bottom: 100px;
}




/*********** Top ***********/

/*추천 리스트 드롭다운 */

/* 드롭다운 스타일 */
.dropdown {
    position: absolute;
    background: white;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    color: var(--podium-cds-color-black);
    width: 150px;
    padding: 12px;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
}

.dropdown button {
    background: none;
    border: none;
    width: 100%;
    padding: 8px;
    text-align: left;
    cursor: pointer;
    font-size: 15px;
    font-weight: 600;


}

.dropdown button:hover {
    background: #f0f0f0;
}

/* 드롭다운이 보일 때 */
.dropdown.show {
    display: block;
}


/* 정렬 버튼 */
.sort-btn {
    background: none;
    border: none;
    cursor: pointer;
    padding: 8px 12px;
    font-size: 15px;
    font-weight: 600;
}




/******** 네비게이션 스타일 ********/
.left-nav-wrapper {
    width: 200px;
    max-height: 320px; /* 스크롤이 생기는 높이 */
    border-radius: 8px;
    padding: 10px;
    display: block;
}






/******** 제품 정보 ********/
.product-card-info{
    font-size: 16px;
}

.product-card-titles div, p{
    margin-top: 10px;
}

.product-card{
    width: 280px;
    font-weight: 600;
    margin-bottom: 60px;
}

.product-card img{
    width: 280px;
    height: 350px;
    object-fit: cover;
    margin-right: 8px;
}

.product-new{
    font-size: 18px;
    font-weight: 600;
    color:#9e3500;
}

.product-card-subtitle, .product-card-item{
    color: var(--podium-cds-typography-body2-strong);
    font-weight: 400;
}

/* Link 기본 스타일 제거 */
a, .react-router-dom a {
    text-decoration: none;  /* 밑줄 제거 */
    color: inherit;         /* 기본 텍스트 색상으로 설정 */
}







